import ArticleList from './components/ArticleList'
import { cloudFunction } from '@/utils/cloud'

export default {
  components: { ArticleList },
  data () {
    return {
      loading: false,
      hasValue: false,
      searchValue: '',
      title: '十+搜索',
      articleList: []
    }
  },
  methods: {
    // 设置搜索参数
    async setSearchValue (e) {
      this.loading = true
      this.searchValue = e.detail.value
      this.hasValue = e.detail.value.length === 0 ? false : true
      console.log('sss', this.hasValue)
      if (Boolean(e.detail.value)) {
        const [err, data] = await cloudFunction('getArticles', { title: e.detail.value })
        if (!err) {
          this.articleList = data.data
        }
      } else {
        this.articleList = []
      }
      this.loading = false

    },
    toDetail (article) {
      uni.navigateTo({
        url: `/pages/article/detail?title=${article.title}`
      })
    }
  }
}
